// Импортируем глобальные переменные
@import "fonts";
@import "master";

// Utilities
html,
body {
  height: 100%;
  padding: 0;
}
body {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 21px;
  font-family: "Gotham", sans-serif;
  font-weight: 500;
}
.main-container {
  flex: 1 0 auto;
}
.main-footer {
  flex-shrink: 0;
}

img {
  max-width: 100%;
}

.text-small {
  font-size: 0.8571rem;
  line-height: 17.9998px;
  font-weight: 500;
}
.text-subtitle {
  font-size: 1.2857rem;
  font-weight: bold;
  line-height: 27px;
}
.text-title {
  font-family: "Cera Round Pro", sans-serif;
  font-size: 2rem;
  font-weight: 900;
  line-height: 34px;
}

.title-line {
  position: relative;
  span {
    position: relative;
    z-index: 1;
    padding-right: 12px;
  }
  &::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 6px;
    height: 1px;
    width: 100%;
    background: $white;
    z-index: 0;
  }
}

// Main styles
body {
  background-color: #f2f2f2;
  background-image: url("../assets/header-rounded-rec.svg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 100% 342px;
}
.main-header {
  &__top {
    background-color: $main;
    color: $white;
    padding-top: 10px;
    padding-bottom: 20px;
  }
  &__bottom {
    color: $white;
    padding: 15px 0;
  }
}
.main-footer {
  margin-top: 50px;
  color: $white;

  &__top {
    padding: 50px 0;
  }
  &__bottom {
    padding: 50px 0;
  }

  &__logo-wrap {
    margin-bottom: 30px;
  }
  &__social-links {
    margin-bottom: 38px;
  }
  &__social-link {
    font-size: 30px;
    color: $white !important;
    text-decoration: none !important;
    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  &__nav-title {
    display: block;
    margin-bottom: 20px;
  }
  &__nav-big {
    &-item {
      margin-bottom: 30px;
      a {
        color: $white-high;
      }
    }
  }
  &__nav-small {
    &-item {
      min-width: 50%;
      margin-bottom: 15px;
      a {
        color: $white-high;
      }
    }
  }
}
.main-container {
  &__content {
    padding: 0px 30px;
    background: #ffffff;
    border-radius: 30px;
    margin-bottom: 0px;
    margin-left: -30px;
    margin-right: -30px;
  }
}
